










































































import { Component, Vue, Watch } from 'vue-property-decorator';

import { Components, Drivers } from './Components'
import { HEADER, SIDEBAR, PAGE, MODAL } from './Components'
import { TComponent, imports } from './Components'

// placeholder data
import SettingData from './placeholders/settings.json'
import NoteListData from './placeholders/NoteList.json'

import Bus from './Bus'
import Console from './Console'

import MDriver from './components/drivers/MDriver.vue'

@Component({
    components: imports
})
export default class App extends Vue 
{
    // component state
    private State: { [component: string]: { [property: string]: any } } = 
    {
        [SIDEBAR]: {
            State: Components.App[SIDEBAR].States.HIDDEN, 
            Content: null, Data: null
        },
        [HEADER]: { 
            Trigger: null, Hide: []
        },
        [MODAL]: { 
            On: false, Title: '', 
            Content: null, Data: null
        },
        [PAGE]: { 
            Title: '', Narrow: false,
            Content: null, Data: null
        }
    }

    private Data: {[component: string]: any} = {}

    // state update
    @Watch('State', {deep: true}) 
    private Controls(n: object, o: object)
    {
        console.log('state updated')
    }

    // event handlers
    private readonly Events = 
    {
        // [Bus.Events.Note.UPDATE]: (textData: {md: string, html: string}) =>
        // {
        //     // write to file... //
        //     // then back to viewing
        //     this.SetProps(Components[PAGE].NOTEVIEW, {
        //         markdown: textData.md, html: textData.html
        //     })
        // }
    }

    // initialization / main thread
    mounted() // essential that this happens after children are created
    {
        Console.Expose({
            'state': this.State,
            'components': Components
        }); 
        (window as any).debug = Console.Access

        Bus.Register(App, this.Events)

        this.SetProps(Components[PAGE].NOTEVIEW, {
            markdown: 'cycek', html: ''
        })
        this.Load(Components.App[PAGE], Components[PAGE].NOTEVIEW)

        this.Actions.Driver.Load(Drivers.App.Fetch, Drivers[Drivers.App.Fetch.name].HTTP)

        this.Actions.FETCH('sexxx').then(answer => this.SetProps(Components.Page.NOTEVIEW, {
            markdown: answer, html: answer
        }))

    }

    // helper methods called in context of a particular component
    private Actions = {
        [SIDEBAR]: {
            Toggle: () => {
                if (this.State[SIDEBAR].State == Sidebar.States.OPEN)
                {
                    this.State[SIDEBAR].State = Sidebar.States.PEEK
                    this.State[HEADER].Trigger = null          
                }
                else
                {
                    this.State[SIDEBAR].State = Sidebar.States.OPEN
                    // this is a logical violation and can be done even better but throwing it here for now to clean things up
                    this.State[HEADER].Trigger = Header.Items.LOGO

                }
            }
        },
        [HEADER]: {
            LockContext: () => this.State.Header.Hide.push(
                Header.Items.NOTE,
                Header.Items.EXAM,
            )
        },
        Driver: {
            Configure: (driver: TComponent, options: object) => this.SetProp(driver, 'options', options),

            Drive: (driver: TComponent, input: any) => this.SetProp(driver, 'input', input),

            Load: (driver: TComponent, type: TComponent, options: object = null) => {
                if (this.State[driver.name])
                    this.Load(driver, type)
                else
                    this.State[driver.name] = { Content: type, Data: null }

                if (options) this.Actions.Driver.Configure(driver, options)
            }
        },
        FETCH: (location: string) =>
        {
            const _ = Bus.Promise(App, MDriver.Events.ANSWER)
            
            this.Actions.Driver.Drive(Drivers.App.Fetch, location)

            return _
        }
    }

    // crucial methods

    private Reload(component: TComponent)
    {
        const current = this.State[component.name].Content
        this.State[component.name].Content = null
        this.State[component.name].Content = current
    }

    private Load(component: TComponent, content: TComponent)
    {
        this.State[component.name].Data = this.Data[content.name]       
        this.State[component.name].Content = content.name
    }

    private SetProp(content: TComponent, property: string, value)
    {
        if (this.Data[content.name])
            this.Data[content.name][property] = value
        else
            this.Data[content.name] = { [property]: value }
    }

    private SetProps(content: TComponent, values: { [property: string]: any} )
    {
        if (this.Data[content.name])
            Object.assign(this.Data[content.name], values)
        else
            this.Data[content.name] = values
    }

    // macros for render
    private HEADER = HEADER; private SIDEBAR = SIDEBAR; private PAGE = PAGE; private MODAL = MODAL; private Drivers = Drivers
}

