

























import { Vue, Component, Prop } from 'vue-property-decorator'
import Bus from '../Bus'

@Component
// the design is enforced by the lack of ability to access static properties in the template and there is actually no other valid way to do it (fuck vue)
export default class Header extends Vue
{
    public static readonly Items = {
        LOGO: 1,
        NOTE: 2,
        EXAM: 3,
        MENU: 4,
        NONE: 666
    }
    private readonly pItems = Header.Items // only for template items outside of panel eg LOGO, may still be changed
    
    private readonly Panel = [
        {id: Header.Items.NOTE, awesomeClassName: 'book'},
        {id: Header.Items.EXAM, awesomeClassName: 'pen'},
        {id: Header.Items.MENU, awesomeClassName: 'bars'},
    ]

    // can also be implemented as an object where the key is the item when more than 1 items need be highlighted at once; but the simplifies the exchange
    @Prop(Number) trigger
    @Prop(Array) hide // quick and easy way to get rid of selected header items if they cant be accessed from the current context

    private navigate = (item: number) => Bus.Emit(12, item)
}


