


import { Vue, Component, Prop, Watch } from 'vue-property-decorator'
import Bus from '../../Bus'

@Component
export default class MDriver extends Vue
{
    static STUB = 'no data'
    static Events = {
        ANSWER: Bus.GetEventNumber()
    }

    @Watch('input', { immediate: true })
    private Answer()
    {
        Bus.Emit(MDriver.Events.ANSWER, this.Output(this.input))
    }

    protected Output(input: any): any // OVERRIDE THIS
    {
        return MDriver.STUB
    }

    @Prop(Object) options
    @Prop() input
}

