@Component({
    components: { List }
})
export default class NoteList extends Mixins(MContent, MPage)
{
    private readonly Menu = [
        {type: List.MenuItems.SEARCH, title: 'Search'}, 
        {type: List.MenuItems.SELECT, title: 'Category'}, 
        {type: List.MenuItems.SELECT, title: 'Tag'},
