












































import { Vue, Component, Mixins, Prop } from 'vue-property-decorator'

@Component
export default class List extends Vue
{
    public static readonly MenuItems = {
        SEARCH: 1,
        SELECT: 2
    }
    private readonly pMenuItems = List.MenuItems


    // no way to type check against this data at runtime so any developer mistakes will result in a given item not rendering
    // the implementing components need to be fully aware of the interface and simplify it for higher users
    @Prop({ type: Array, default: () => [] }) menu 
    @Prop({ type: Array, default: () =>  [] }) actions
    @Prop({ type: Array, default: () => [] }) items
    @Prop({ type: Object, default: () => { return { sortBy: [], viewBy: [] }} }) adjust 
}

