



















import { Vue, Component, Prop } from 'vue-property-decorator'
import Parse from './Markdown'

@Component
export default class Read extends Vue
{
    // can be exposed but i do not see the need to
    public static readonly States = {
        READ: 1,
        EDIT: 2
    }
    private readonly pStates = Read.States

    private State = Read.States.EDIT

    private edit()
    {
        this.$emit('edit', { md: this.markdown, html: Parse(this.markdown) }) // replace with Parse(this.text.md)
        this.State = Read.States.READ
    }

    @Prop(String) title
    @Prop(String) markdown
    @Prop(String) html
    @Prop(Object) mdclasses
}

