
// work in progress

import { TComponent } from './Components'
export type TEvent = number

export default class Bus 
{
    // only one callback allowed per component per event (otherwise have to deal with branding each callback separately to not confuse them)
    private static Events: { [event: number]: { [component: string]: Function } } = {} // TS even MORE retarded: cannot use a type alias as a key type
    private static CurrentEventNumber = 1

    static Emit(event: TEvent, data: any = null)
    {
        if (!Bus.Events[event]) throw new Error('not implemented')

        for (const componentHandler of Object.values(Bus.Events[event]))
        {
            componentHandler(data)
        }
    }

    static On(id: TComponent, event: TEvent, action: Function)
    {
        if (!Bus.Events[event])
            Bus.Events[event] = { [id.name]: action }
        else
            Bus.Events[event][id.name] = action
    }

    static Off(id: TComponent, event: TEvent)
    {
        if (!Bus.Events[event]) return 
        
        delete Bus.Events[event][id.name]

        if (!Bus.Events[event])
            delete Bus.Events[event]
    }

    static Register(id: TComponent, events: { [event: number]: Function} )
    {
        for (const event of Object.keys(events))
        {
            Bus.On(id, event, events[event])
        }
    }

    static React(id: TComponent, event: TEvent, action: Function)
    {
        Bus.On(id, event, function(data) {
            action(data)
            Bus.Off(id, event)
        })
    }

    static Promise(id: TComponent, event: TEvent)
    {
        return new Promise(resolve => Bus.React(id, event, resolve))
    }

    static GetEventNumber(): number
    {
        return Bus.CurrentEventNumber++
    }
}