// component registry prototype
// note: any content deeper than the components directly embedded in App have no name macro to avoid name conflicts
// you can always produce such a macro for any component, but even now its name can be obtained by referencing Components[<comp>].<child>.name

import { Vue } from 'vue-property-decorator'
export type TComponent = typeof Vue

type TRegistry = { [component: string]: { [child: string ]: TComponent } }

import Header from './components/Header.vue'
export const HEADER = Header.name

import Sidebar from './components/sidebar/Sidebar.vue'
export const SIDEBAR = Sidebar.name

import Page from './components/page/Page.vue'
export const PAGE = Page.name

import Modal from './components/modal/Modal.vue'
export const MODAL = Modal.name

export const Components: TRegistry = {}

Components.App = {
    [HEADER]: Header,
    [SIDEBAR]: Sidebar,
    [PAGE]: Page,
    [MODAL]: Modal,
}

import Settings from './components/sidebar/Settings.vue'

Components[SIDEBAR] = {
    SETTINGS: Settings
}

import NoteList from './content/page/NoteList.vue'
import NoteView from './content/page/NoteView.vue'

Components[PAGE] = {
    NOTELIST: NoteList,
    NOTEVIEW: NoteView
}

import Info from './components/modal/Info.vue'
import Set from './components/modal/Set.vue'
import Select from './components/modal/Select.vue'

Components[MODAL] = {
    INFO: Info,
    SET: Set,
    SELECT: Select
}

// DRIVER COMPONENTS

export const Drivers: TRegistry = { App: {} }

class Fetch extends Vue {}
Drivers.App.Fetch = Fetch

import HTTP from './components/drivers/fetch/HTTP.vue'

Drivers[Fetch.name] = {
    HTTP: HTTP
}


// component registry for components imported in this file

export const imports: { [c: string]: TComponent } = 
{
    'vue-header': Components.App[HEADER],
    'vue-sidebar': Components.App[SIDEBAR], 
    'vue-page': Components.App[PAGE],
    'vue-modal': Components.App[MODAL],
}

function addImports(registry: TRegistry)
{
    for (const component of Object.values(registry))
    {
        for (const child of Object.values(component))
        {
            imports[child.name] = child
        }
    }
}

addImports(Object.assign({ }, Components, { App: {} })) // do not add App-level components because they are predefined in the imports object
addImports(Object.assign({ }, Drivers, { App: {} })) // do not add App-level drivers because they exist only logically